import React from 'react'
import s from './CoffeeAnimation.module.css'
import {TweenMax, TweenLite, TimelineLite} from 'gsap'
import noder from"../../utils/noder"

class CoffeeAnimation extends React.Component {
    
    componentDidMount(){
        setTimeout(()=>{
            this.initAnimation()
        },2000)
    }

    getParentElDimensions(){
        let parentEl = document.querySelector(this.props.parentElSelector)
        return parentEl.getBoundingClientRect()
    }

    getMaxElementsPerRow(elWidth){
        let maxWidth = this.getParentElDimensions().width
        return Math.floor(maxWidth / elWidth)
    }

    showElements(elements){
        elements.forEach(function(el) { el.style.display = "block"; });
    }

    getNumRows(maxElementsPerRow, numElements){
        return Math.floor(numElements / maxElementsPerRow)
    }

    getYValArr(numRows, mult){
        let valArr = []

        let i = 1

        for(;i <= numRows;i++){
            valArr.push(i * mult)
        }

        return valArr.reverse()
    }

    positionElements(els){
        
        const maxElementsPerRow = this.getMaxElementsPerRow(8)
        const numRows = this.getNumRows(maxElementsPerRow, els.length)
        
        let yValArr = this.getYValArr(numRows, 16)
        let xInt = 0
        let yInt = 0

        yValArr = yValArr.map((el) => {
                return el - 170
            })

        els.forEach((el, i)=>{
            xInt++
            var xpos = xInt * 16
            var rotation = (Math.round(Math.random() * 47))
            
            if(Math.round(Math.random() * 47) % 2 === 0) {
                rotation = rotation * -1
            }

            el.style.display = 'block'
            el.style.transform = `translate(${xpos}px, ${yValArr[yInt]}px) rotate(${rotation}deg)`

            if(i !== 0 && i % maxElementsPerRow === 0) {

               xInt = 0
               yInt++
            }
        })
    }

    triggerTimeLineAnimation(){

        var currentKey = this.refs.dtCurrentKey
        var currentVal = this.refs.ddCurrentVal
        var roastKey = this.refs.dtRoastKey
        var roastVal = this.refs.dtRoastVal
        var dtRegionKey = this.refs.dtRegionKey
        var dtRegionVal = this.refs.dtRegionVal

        var coffeeText1 = this.refs.coffeeText1
        var coffeeText2 = this.refs.coffeeText2
        var coffeeText3 = this.refs.coffeeText3

        var tl = new TimelineLite()
        tl.add( TweenLite.to(coffeeText1, 0.5, {opacity:1}) )
        tl.add( TweenLite.to(coffeeText2, 0.5, {opacity:1}) )
        tl.add( TweenLite.to(coffeeText3, 0.5, {opacity:1}) )
        tl.add( TweenLite.to([coffeeText1,coffeeText2,coffeeText3], 1, {opacity:0}) )
        tl.add( TweenLite.to(currentKey, 0.3, {left:100}) )
        tl.add( TweenLite.to(currentVal, 0.3, {left:100}) )
        tl.add( TweenLite.to(roastKey, 0.3, {left:100}) )
        tl.add( TweenLite.to(roastVal, 0.3, {left:100}) )
        tl.add( TweenLite.to(dtRegionKey, 0.3, {left:100}) )
        tl.add( TweenLite.to(dtRegionVal, 0.3, {left:100}) )
    }

    triggerAnimation(elGroup, step){

        step = step || 1

        var elAccumulator = 0

        elGroup.forEach((el, i) => {
            
            var rotation = (Math.round(Math.random() * 47))
            
            if(Math.round(Math.random() * 47) % 2 === 0) {
                rotation = rotation * -1
            }

            let duration = ( (Math.round(Math.random() * 50)) * 15 ) / 1700
            var delay = (Math.round(Math.random() * 5) - (i * 2) ) / 4400;

            if(delay < 0) {
                delay = delay * -1
            }

            TweenLite.to(el, duration, {
                display:'block',
                y:"+=170",
                rotation:`+=${rotation}`,
                delay:delay,
                ease:Back.easeInOut.config(0.5),
                onComplete: () => {
                    elAccumulator++

                    if(elAccumulator === elGroup.length ) {
                        if(step !== 2) {
                            setTimeout(()=>{
                                this.triggerAnimation(elGroup, 2)
                            },1500)
                            
                        } else {

                            this.triggerTimeLineAnimation()
                        }
                    } 
                }
            })

        })

    }

    initAnimation(){

        let elementPool = noder.createElementPoolFromSelector(`.${s['coffeeBean']}`, (this.getMaxElementsPerRow(8) * 8) ) 
        
        noder.appendChildElements(`#svg-canvas`, elementPool)

        let elements = document.querySelectorAll(`.${s['coffeeBean']}`)

        this.positionElements(elements)

        this.triggerAnimation(elements)

    }

    render(){
        return (
            <div className={s['coffeeAnimationCanvas']}>
                <svg id="svg-canvas" className={s['svgCanvas']}>
                  <g className={s['coffeeBean']} stroke="none" strokeWidth="1" fill="#fff" fillRule="evenodd">
                      <ellipse id="Oval" stroke="#000000" cx="11.9544208" cy="8.07869038" rx="7.83723475" ry="5.92130962"></ellipse>
                      <path d="M9,9.35444828 C9.93002247,6.77615324 11.2745143,8.55355696 12.3899763,9.20320089 C13.5874249,9.90059371 14.4108553,9.442405 15.484375,8.28438185" id="Path-4" stroke="#000000"></path>
                  </g>
                </svg>
                <div className={s['coffeeInfo']}>
                    <h3 ref="coffeeText1" className={s['coffeeText']}>I</h3>
                    <h3 ref="coffeeText2" className={s['coffeeText']}>LOVE</h3>
                    <h3 ref="coffeeText3" className={s['coffeeText']}>COFFEE</h3>
                    <dl className={s['coffeeInfoData']}>
                        <dt ref="dtCurrentKey">Currently drinking</dt>
                        <dd ref="ddCurrentVal">Markt brand Arabica</dd>
                        <dt ref="dtRoastKey">Roast</dt>
                        <dd ref="dtRoastVal">Dark</dd>
                        <dt ref="dtRegionKey">Source Region(s)</dt>
                        <dd ref="dtRegionVal">South America</dd>
                    </dl>
                </div>
            </div>
        )
    }
}

export default CoffeeAnimation


