import React from 'react'
import GatsbyLink from 'gatsby-link'
import s from './Link.module.css'


const Link = (props) =>{

  return <GatsbyLink className={s['link']} to={props.to} onClick={props.handleClick}>{props.children}</GatsbyLink>
}

export default Link


