import React from "react"
import * as PropTypes from "prop-types"
import CoffeeAnimation from "../CoffeeAnimation";
import s from "./InfoGrid.module.css"

class InfoGrid extends React.Component {
  render() {

    return (
      <div className={s['infoGrid']}>
        <div className={`coffee ${s['infoGridItem']}`}>
          <CoffeeAnimation parentElSelector=".coffee" />
        </div>
        <div className={`weather ${s['infoGridItem']}`}>
          <h3>Weather here </h3>
        </div>
        <div className={`weather ${s['infoGridItem']}`}>
          <h3>baseball here </h3>
        </div>
      </div>
    )
  }
}

export default InfoGrid


